import React from "react";
import { Navbar, NavbarBrand, Nav, NavItem, NavLink,Collapse,NavbarToggler } from "reactstrap";
import "./header.css";
class Header extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }


  render(){
  return (
      <div className="header" style={{backgroundColor:"rgb(255,255,255)"}}>
      <Navbar light expand="md" navbar className='container-fluid'>
        <NavbarBrand href="\">
          <img
            src={require('../images/logo.png')}
            width="110px"
            height="45px"
            alt="404 error"
          />
        </NavbarBrand>
        <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
        <Nav navbar className="ml-auto nav">
          <NavItem className="mt-2">
            <p>Already have an account?</p>
          </NavItem>
          <NavItem>
            <NavLink href="\login" className="login">Login In</NavLink>
          </NavItem>
        </Nav>
        </Collapse>
      </Navbar>
      </div>
  )
  }
};

export default Header;
