import React from "react";
import { Row, Col } from "reactstrap";
import "./section.css";
const Section = () => {
  return (
    <div className="container-fluid">
      <Row className="d-flex justify-content-center">
        <p style={{ fontSize: "17px", marginTop: "1em", fontWeight: "500" }}>
          Trusted by 5M<span style={{ paddingTop: "-5px" }}>+</span>businesses
        </p>
      </Row>
      <Row className="logo-row">
        <Col className="col-lg-2 col-md-3" style={{ marginLeft: "-40px" }}>
          <img
            src={require("../images/mslogo.png")}
            width="200px"
            height="100px"
            className="logo-1"
          />
        </Col>
        <Col
          className="col-lg-2 col-md-3"
          style={{ marginTop: "30px", marginLeft: "20px" }}
        >
          <img
            src={require("../images/airbnb.png")}
            width="130px"
            height="40px"
            className="logo-2"
          />
        </Col>
        <Col className="col-lg-2 col-md-3" style={{ marginTop: "22px" }}>
          <img src={require("../images/GE.png")} width="70px" height="60px" className="logo-3" />
        </Col>
        <Col className="col-lg-2 col-md-3" style={{ marginLeft: "-90px" }}>
          <img
            src={require("../images/automattic.png")}
            width="200px"
            height="100px"
            className="logo-4"
          />
        </Col>
        <Col
          className="col-lg-2 hide"
          style={{ marginTop: "10px", marginLeft: "80px" }}
        >
          <img
            src={require("../images/bissell-logo.png")}
            width="90px"
            height="70px"
          />
        </Col>
        <Col
          className="col-lg-2 hide"
          style={{ marginTop: "10px", marginLeft: "-20px" }}
        >
          <img
            src={require("../images/coty.png")}
            width="100px"
            height="70px"
          />
        </Col>
      </Row>
      <div className="container mt-5 mb-3 section-heading">
        <p style={{ fontSize: "18px", fontWeight: "500" }}>
          More than 60k jobs are posted on Upwork every week
        </p>
      </div>
    <div className=" section-column">
      <div className="d-flex column">
        <div className="mx-2">
        <img
              src={require("../images/section-logo.png")}
              width="130px"
              height="100px"
              style={{borderRadius:"50%"}}
              />
        </div>
        <div>
        <span style={{fontWeight:'500', display:"block"}}>Engage with confidence</span>
            <span>Only pay for work you authorize, with secure billing and invoices.</span>
        </div>
      </div>
      <div className="d-flex column">
        <div className="mx-2">
        <img
              src={require("../images/section-logo.png")}
              width="130px"
              height="100px"
              style={{borderRadius:"50%"}}
              />
        </div>
        <div>
            <span style={{fontWeight:'500', display:"block"}}>Engage with confidence</span>
            <span>Only pay for work you authorize, with secure billing and invoices.</span>
        </div>
      </div>
      <div className="d-flex">
        <div className="mx-2">
        <img
              src={require("../images/section-logo.png")}
              width="130px"
              height="100px"
              style={{borderRadius:"50%"}}
              />
        </div>
        <div>
        <span style={{fontWeight:'500', display:"block"}}>Engage with confidence</span>
            <span>Only pay for work you authorize, with secure billing and invoices.</span>
        </div>
      </div>
</div>
    </div>
  );
};

export default Section;
