import React from 'react';
import {Button, Form, label, Input, Row, Col} from "reactstrap";
import "./signup.css";
const SignUp = () => {
    return ( <div className="main-div" style={{backgroundColor: "rgb(29,67,84)", height: '25em'}}>
        <div style={{paddingTop:"30px"}}>
            <Form className="form" style={{width:"100%",maxWidth:"40vw",margin:"auto",textAlign:'center',padding:"30px",paddingRight:"40px",paddingLeft:"40px", backgroundColor:"rgb(255,255,255)"}}>
                <p style={{fontSize:"28px"}}>Get your free account </p>
                <Row className="mt-2 main">
                    <Col className="col-12 col-md-6">
                <Input type="text" placeholder="First name"/>
                </Col>
                    <Col className="col-12 col-md-6">
                <Input type="text" placeholder="Last name"/>
                </Col>
                </Row>
                <Row >
                    <Col>
                    <Input type="text" placeholder="Work email address"/>
                    </Col>
                </Row>
                <Button className="mt-5" style={{backgroundColor:"rgb(55,160,0)", borderRadius:"2px",padding:"7px 22px 7px 22px",fontWeight:"500", border:"none"}}>Sign Me Up</Button>
            </Form>
            </div>
    </div> );
}
 
export default SignUp;